<template>
  <view class="wallet-container">
    <!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
    <view class="nav-bar">
      <view class="nav-back" @click="goBack">
        <text class="iconfont">‚Äπ</text>
      </view>
      <view class="nav-title">ÊàëÁöÑÈí±ÂåÖ</view>
      <view class="nav-placeholder"></view>
    </view>

    <!-- Èí±ÂåÖÂç°Áâá -->
    <view class="wallet-card">
      <view class="card-header">
        <text class="card-title">Ë¥¶Êà∑‰ΩôÈ¢ù</text>
        <text class="card-unit">ÂÖÉ</text>
      </view>
      <view class="card-balance">
        <text class="balance-integer">{{ balanceInteger }}</text>
        <text class="balance-decimal">.{{ balanceDecimal }}</text>
      </view>
      <view class="card-actions">
        <button class="btn-recharge" @click="goToRecharge">
          <text class="btn-icon">üí∞</text>
          <text>ÂÖÖÂÄº</text>
        </button>
        <button class="btn-withdraw" @click="handleWithdraw">
          <text class="btn-icon">üí∏</text>
          <text>ÊèêÁé∞</text>
        </button>
      </view>
    </view>

    <!-- Âø´Êç∑ÂÖÖÂÄº -->
    <view class="quick-recharge-section">
      <view class="section-title">Âø´Êç∑ÂÖÖÂÄº</view>
      <view class="amount-grid">
        <view
          class="amount-item"
          v-for="amount in quickAmounts"
          :key="amount"
          @click="quickRecharge(amount)"
        >
          <text class="amount-value">{{ amount }}</text>
          <text class="amount-unit">ÂÖÉ</text>
        </view>
      </view>
    </view>

    <!-- ‰∫§ÊòìËÆ∞ÂΩï -->
    <view class="transactions-section">
      <view class="section-header">
        <text class="section-title">ËµÑÈáëÊµÅÊ∞¥</text>
        <text class="section-more" @click="loadMore">Êü•ÁúãÂÖ®ÈÉ® ‚Ä∫</text>
      </view>

      <!-- ‰∫§ÊòìÂàóË°® -->
      <scroll-view
        scroll-y
        class="transactions-list"
        @scrolltolower="loadMore"
        v-if="transactions.length > 0"
      >
        <view
          class="transaction-item"
          v-for="item in transactions"
          :key="item.id"
        >
          <view class="transaction-icon">
            <text>{{ getTransactionIcon(item.type) }}</text>
          </view>
          <view class="transaction-info">
            <text class="transaction-title">{{ item.typeName }}</text>
            <text class="transaction-time">{{ formatTime(item.createTime) }}</text>
          </view>
          <view class="transaction-amount" :class="getAmountClass(item.type)">
            <text>{{ getAmountPrefix(item.type) }}{{ item.amount }}</text>
          </view>
        </view>
      </scroll-view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view class="empty-state" v-if="transactions.length === 0 && !loading">
        <text class="empty-icon">üí≥</text>
        <text class="empty-text">ÊöÇÊó†‰∫§ÊòìËÆ∞ÂΩï</text>
      </view>

      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <view class="loading-state" v-if="loading">
        <text>Âä†ËΩΩ‰∏≠...</text>
      </view>
    </view>
  </view>
</template>

<script>
import { getWalletBalance, getTransactions, recharge } from '@/api/wallet.js'

export default {
  data() {
    return {
      balance: 0, // ‰ΩôÈ¢ù
      transactions: [], // ‰∫§ÊòìËÆ∞ÂΩï
      loading: false,
      quickAmounts: [10, 50, 100, 200, 500, 1000], // Âø´Êç∑ÂÖÖÂÄºÈáëÈ¢ù
      page: 1,
      size: 10,
      hasMore: true
    }
  },

  computed: {
    // ‰ΩôÈ¢ùÊï¥Êï∞ÈÉ®ÂàÜ
    balanceInteger() {
      return Math.floor(this.balance).toString()
    },
    // ‰ΩôÈ¢ùÂ∞èÊï∞ÈÉ®ÂàÜ
    balanceDecimal() {
      const decimal = (this.balance % 1).toFixed(2)
      return decimal.substring(2) // ÂéªÊéâ"0."
    }
  },

  onLoad() {
    this.loadWalletBalance()
    this.loadTransactions()
  },

  onShow() {
    // ‰ªéÂÖÖÂÄºÈ°µËøîÂõûÊó∂Âà∑Êñ∞‰ΩôÈ¢ù
    this.loadWalletBalance()
  },

  onPullDownRefresh() {
    this.page = 1
    this.hasMore = true
    Promise.all([this.loadWalletBalance(), this.loadTransactions()]).then(() => {
      uni.stopPullDownRefresh()
    })
  },

  methods: {
    /**
     * Âä†ËΩΩÈí±ÂåÖ‰ΩôÈ¢ù
     */
    async loadWalletBalance() {
      try {
        const res = await getWalletBalance()

        if (res.code === 200) {
          this.balance = res.data || 0
          console.log('‚úÖ Èí±ÂåÖ‰ΩôÈ¢ù:', this.balance)
        }
      } catch (error) {
        console.error('‚ùå Ëé∑Âèñ‰ΩôÈ¢ùÂ§±Ë¥•:', error)
      }
    },

    /**
     * Âä†ËΩΩ‰∫§ÊòìËÆ∞ÂΩï
     */
    async loadTransactions() {
      if (this.loading || !this.hasMore) return

      try {
        this.loading = true

        const res = await getTransactions({
          page: this.page,
          size: this.size
        })

        if (res.code === 200) {
          const records = res.data.records || []

          if (this.page === 1) {
            this.transactions = records
          } else {
            this.transactions = [...this.transactions, ...records]
          }

          // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
          this.hasMore = records.length >= this.size
          this.page++

          console.log('‚úÖ ‰∫§ÊòìËÆ∞ÂΩï:', this.transactions)
        }
      } catch (error) {
        console.error('‚ùå Ëé∑Âèñ‰∫§ÊòìËÆ∞ÂΩïÂ§±Ë¥•:', error)
      } finally {
        this.loading = false
      }
    },

    /**
     * Âä†ËΩΩÊõ¥Â§ö
     */
    loadMore() {
      if (this.hasMore) {
        this.loadTransactions()
      } else {
        uni.showToast({
          title: 'Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü',
          icon: 'none'
        })
      }
    },

    /**
     * Ë∑≥ËΩ¨Âà∞ÂÖÖÂÄºÈ°µÈù¢
     */
    goToRecharge() {
      uni.navigateTo({
        url: '/pages/wallet/recharge'
      })
    },

    /**
     * Âø´Êç∑ÂÖÖÂÄº
     */
    quickRecharge(amount) {
      uni.showModal({
        title: 'Á°ÆËÆ§ÂÖÖÂÄº',
        content: `Á°ÆÂÆöË¶ÅÂÖÖÂÄº ${amount} ÂÖÉÂêóÔºü`,
        success: (res) => {
          if (res.confirm) {
            this.doRecharge(amount)
          }
        }
      })
    },

    /**
     * ÊâßË°åÂÖÖÂÄº
     */
    async doRecharge(amount) {
      try {
        uni.showLoading({ title: 'ÂÖÖÂÄº‰∏≠...' })

        const res = await recharge({ amount })

        uni.hideLoading()

        if (res.code === 200) {
          uni.showToast({
            title: 'ÂÖÖÂÄºÊàêÂäü',
            icon: 'success'
          })

          // Âà∑Êñ∞‰ΩôÈ¢ùÂíå‰∫§ÊòìËÆ∞ÂΩï
          await this.loadWalletBalance()
          this.page = 1
          await this.loadTransactions()
        } else {
          uni.showToast({
            title: res.message || 'ÂÖÖÂÄºÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        uni.hideLoading()
        console.error('‚ùå ÂÖÖÂÄºÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'ÂÖÖÂÄºÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
          icon: 'none'
        })
      }
    },

    /**
     * ÊèêÁé∞ÂäüËÉΩÔºàÂºÄÂèë‰∏≠Ôºâ
     */
    handleWithdraw() {
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'ÊèêÁé∞ÂäüËÉΩÂºÄÂèë‰∏≠ÔºåÊï¨ËØ∑ÊúüÂæÖ',
        showCancel: false
      })
    },

    /**
     * Ëé∑Âèñ‰∫§ÊòìÂõæÊ†á
     */
    getTransactionIcon(type) {
      // type: 1-Êî∂ÂÖ•, 2-ÊîØÂá∫
      return type === 1 ? 'üí∞' : 'üí∏'
    },

    /**
     * Ëé∑ÂèñÈáëÈ¢ùÊ†∑ÂºèÁ±ªÂêç
     */
    getAmountClass(type) {
      return type === 1 ? 'amount-income' : 'amount-expense'
    },

    /**
     * Ëé∑ÂèñÈáëÈ¢ùÂâçÁºÄ
     */
    getAmountPrefix(type) {
      return type === 1 ? '+' : '-'
    },

    /**
     * Ê†ºÂºèÂåñÊó∂Èó¥
     */
    formatTime(time) {
      if (!time) return ''

      const date = new Date(time)
      const now = new Date()
      const diff = now.getTime() - date.getTime()

      // ‰ªäÂ§©
      if (diff < 24 * 60 * 60 * 1000 && date.getDate() === now.getDate()) {
        return '‰ªäÂ§© ' + this.formatHourMinute(time)
      }

      // Êò®Â§©
      const yesterday = new Date(now)
      yesterday.setDate(yesterday.getDate() - 1)
      if (date.getDate() === yesterday.getDate()) {
        return 'Êò®Â§© ' + this.formatHourMinute(time)
      }

      // ÂÖ∂‰ªñÊó•Êúü
      return this.formatDate(time)
    },

    /**
     * Ê†ºÂºèÂåñÊó∂ÂàÜ
     */
    formatHourMinute(time) {
      const date = new Date(time)
      const hours = String(date.getHours()).padStart(2, '0')
      const minutes = String(date.getMinutes()).padStart(2, '0')
      return `${hours}:${minutes}`
    },

    /**
     * Ê†ºÂºèÂåñÊó•Êúü
     */
    formatDate(time) {
      const date = new Date(time)
      const month = String(date.getMonth() + 1).padStart(2, '0')
      const day = String(date.getDate()).padStart(2, '0')
      const hours = String(date.getHours()).padStart(2, '0')
      const minutes = String(date.getMinutes()).padStart(2, '0')
      return `${month}-${day} ${hours}:${minutes}`
    },

    /**
     * ËøîÂõû‰∏ä‰∏ÄÈ°µ
     */
    goBack() {
      uni.navigateBack()
    }
  }
}
</script>

<style lang="scss" scoped>
.wallet-container {
  min-height: 100vh;
  background-color: #f5f5f5;
}

/* ÂØºËà™Ê†è */
.nav-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 44px;
  padding: 0 15px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.nav-back {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: #fff;
  cursor: pointer;
}

.nav-title {
  font-size: 17px;
  font-weight: 600;
  color: #fff;
}

.nav-placeholder {
  width: 40px;
}

/* Èí±ÂåÖÂç°Áâá */
.wallet-card {
  margin: 54px 15px 15px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 16px;
  padding: 30px 20px;
  box-shadow: 0 8px 16px rgba(102, 126, 234, 0.3);
}

.card-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.card-title {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
}

.card-unit {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.6);
}

.card-balance {
  display: flex;
  align-items: baseline;
  margin-bottom: 30px;
}

.balance-integer {
  font-size: 48px;
  font-weight: bold;
  color: #fff;
  line-height: 1;
}

.balance-decimal {
  font-size: 24px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
  margin-left: 4px;
}

.card-actions {
  display: flex;
  gap: 10px;
}

.card-actions button {
  flex: 1;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  font-size: 15px;
  border: none;
}

.btn-recharge {
  background-color: #fff;
  color: #667eea;
  font-weight: 600;
}

.btn-withdraw {
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.btn-icon {
  margin-right: 6px;
  font-size: 16px;
}

/* Âø´Êç∑ÂÖÖÂÄº */
.quick-recharge-section {
  margin: 15px;
  background-color: #fff;
  border-radius: 12px;
  padding: 20px;
}

.section-title {
  font-size: 16px;
  font-weight: 600;
  color: #333;
  margin-bottom: 15px;
}

.amount-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.amount-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15px 10px;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s;
}

.amount-item:active {
  transform: scale(0.95);
}

.amount-value {
  font-size: 20px;
  font-weight: bold;
  color: #333;
  margin-bottom: 4px;
}

.amount-unit {
  font-size: 12px;
  color: #666;
}

/* ‰∫§ÊòìËÆ∞ÂΩï */
.transactions-section {
  margin: 15px;
  background-color: #fff;
  border-radius: 12px;
  padding: 20px;
  max-height: 400px;
  overflow: hidden;
}

.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}

.section-more {
  font-size: 13px;
  color: #667eea;
  cursor: pointer;
}

.transactions-list {
  max-height: 320px;
}

.transaction-item {
  display: flex;
  align-items: center;
  padding: 15px 0;
  border-bottom: 1px solid #f5f5f5;
}

.transaction-item:last-child {
  border-bottom: none;
}

.transaction-icon {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f7fa;
  border-radius: 50%;
  font-size: 18px;
  margin-right: 12px;
}

.transaction-info {
  flex: 1;
}

.transaction-title {
  font-size: 15px;
  color: #333;
  display: block;
  margin-bottom: 4px;
}

.transaction-time {
  font-size: 12px;
  color: #999;
  display: block;
}

.transaction-amount {
  font-size: 16px;
  font-weight: 600;
}

.amount-income {
  color: #07c160;
}

.amount-expense {
  color: #ff4d4f;
}

/* Á©∫Áä∂ÊÄÅ */
.empty-state,
.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
}

.empty-icon {
  font-size: 50px;
  margin-bottom: 12px;
  opacity: 0.5;
}

.empty-text {
  font-size: 14px;
  color: #999;
}
</style>
